:root {
  --swiper-theme-color: #2861CF;
}

html {
  font-family: "Inter", sans-serif;
  line-height: 1.42;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  height: 100%;
  color: #1F2839;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.4;
  background: #FFF;
}

input::-ms-clear {
  display: none;
}

/* Для Safari */
input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

p {
  margin: 0px 0px 26px;
}
@media (max-width: 599px) {
  p {
    margin-bottom: 12px;
  }
}

ul, ol {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

li {
  list-style: none;
}

a {
  color: #1F2839;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
  font-family: "Inter";
  font-weight: 700;
  margin: 0px 0px 32px;
  text-transform: none;
  line-height: 1.2;
}

h1, .h1 {
  font-size: 80px;
  font-size: 5rem;
  margin-top: 0;
  line-height: 1.15;
}
@media (max-width: 960px) {
  h1, .h1 {
    font-size: 42px;
    font-size: 2.625rem;
  }
}
@media (max-width: 599px) {
  h1, .h1 {
    font-size: 32px;
    font-size: 2rem;
  }
}
@media (max-width: 480px) {
  h1, .h1 {
    font-size: 28px;
    font-size: 1.75rem;
  }
}

h2, .h2, .ui-h2 {
  font-size: 54px;
  font-size: 3.375rem;
  line-height: 1.2;
}

h3, .h3, .ui-h3 {
  font-size: 32px;
  font-size: 2rem;
  line-height: 1.2;
}

h4, .h4, .ui-h4 {
  font-size: 24px;
  font-size: 1.5rem;
}
@media (max-width: 599px) {
  h4, .h4, .ui-h4 {
    font-size: 22px;
    font-size: 1.375rem;
  }
}

h5, .h5 {
  font-size: 20px;
  font-size: 1.25rem;
}

h6, .h6, .ui-h5 {
  font-size: 18px;
  font-size: 1.125rem;
  text-transform: none;
  font-weight: 700;
}
@media (max-width: 599px) {
  h6, .h6, .ui-h5 {
    font-size: 16px;
    font-size: 1rem;
  }
}

.ui-font-midi {
  font-size: 16px;
  font-size: 1rem;
}

.ui-font-mini {
  font-size: 14px;
  font-size: 0.875rem;
}

.h20 {
  font-size: 20px;
  font-size: 1.25rem;
}

.ui-strong,
.ui-bold {
  font-weight: 700;
}

.ui-demi {
  font-weight: 600;
}

a {
  text-decoration: none;
  outline: none;
  transition: color 0.3s, border 0.3s;
}

b, strong {
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper a img {
  border: none;
  display: block;
  line-height: 0;
  font-size: 0;
}

/*---- ICONS ---- */
::selection {
  background: #1F2839;
  color: #FFF;
}

::-moz-selection {
  background: #FFFFFF;
  color: #FFF;
}

@media (min-width: 992px) {
  ::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }

  ::-webkit-scrollbar-thumb {
    width: 4px;
    height: 4px;
    background-color: #2861CF;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #4578db;
  }
}
.ui-centered {
  text-align: center;
}

.ui-link {
  color: #2861CF;
  text-decoration-line: underline;
}

.page-title {
  font-weight: 500;
  letter-spacing: -1px;
  text-transform: uppercase;
  margin-bottom: 44px;
}

.ui-more-title {
  display: inline-block;
  font-weight: 500;
  text-transform: uppercase;
  margin-right: 16px;
  margin-bottom: 0;
}

.ui-tabs {
  margin-bottom: 100px;
  font-size: 0;
}

.ui-tab {
  display: inline-block;
  background-color: #FFFFFF;
  margin-right: 10px;
  cursor: pointer;
  color: #1F2839;
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1;
  text-transform: uppercase;
  border-radius: 24px;
  border: 1px solid #9EA5B4;
  padding: 8px 16px;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  margin-bottom: 20px;
}
.ui-tab.active {
  background-color: #2861CF;
  color: #FFFFFF;
  border-color: #2861CF;
}
.ui-tab:hover {
  border-color: #2861CF;
  color: #2861CF;
  background-color: #FFFFFF;
}

.ui-tabs-wrapper {
  display: none;
}
.ui-tabs-wrapper.active {
  display: block;
}

.ui-btn {
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  font-weight: 500;
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.ui-btn-transparent {
  background-color: #FFFFFF;
  border-radius: 24px;
  border: 1px solid #2861CF;
  color: #2861CF;
  height: 30px;
  line-height: 28px;
  padding-left: 12px;
  padding-right: 12px;
  font-size: 17px;
  font-size: 1.0625rem;
}
.ui-btn-transparent:hover {
  background-color: #2861CF;
  color: #FFFFFF;
}
.ui-btn-main {
  padding-left: 40px;
  padding-right: 40px;
  font-size: 20px;
  font-size: 1.25rem;
  height: 64px;
  line-height: 64px;
  border-radius: 40px;
  flex-shrink: 0;
}
.ui-btn-white {
  background-color: #FFFFFF;
}
.ui-btn-white:hover {
  color: #2861CF;
}
.ui-btn-youtube {
  display: inline-block;
  white-space: nowrap;
  background-color: #E53B3B;
  border-radius: 40px;
  padding-left: 40px;
  padding-right: 40px;
  color: #FFFFFF;
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 500;
  height: 64px;
  line-height: 62px;
  text-transform: uppercase;
}
.ui-btn-youtube .icon {
  width: 26px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
  margin-right: 18px;
}
.ui-btn-youtube span {
  vertical-align: middle;
}
.ui-btn-play {
  position: relative;
  width: 80px;
  height: 80px;
  background-color: #FFFFFF;
  border-radius: 50%;
}
.ui-btn-play .icon {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ui-field {
  position: relative;
}
.ui-field label.error {
  position: absolute;
  bottom: -20px;
  top: 100%;
  left: 0;
  color: #B10000;
  font-size: 12px;
  font-size: 0.75rem;
  width: max-content;
}
.ui-field .ui-star {
  position: absolute;
  left: 10px;
  font-size: 18px;
  font-size: 1.125rem;
  cursor: text;
  z-index: 0;
  pointer-events: none;
}
.ui-field input:invalid ~ .ui-star::before {
  content: attr(placeholder);
  position: relative;
  margin-right: 4px;
}
.ui-field input:invalid ~ .ui-star::after {
  content: "*";
  position: relative;
  color: #B10000;
}
.ui-field textarea:invalid ~ .ui-star::before {
  content: attr(placeholder);
  position: relative;
  margin-right: 4px;
}
.ui-field textarea:invalid ~ .ui-star::after {
  content: "*";
  position: relative;
  color: #B10000;
}

.ui-send {
  display: inline-block;
  cursor: pointer;
}
.ui-send span {
  display: inline-block;
  vertical-align: middle;
  line-height: 162.5%;
  letter-spacing: 0.32px;
  letter-spacing: 0.02rem;
  max-width: 200px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  border-bottom: 1px dashed rgba(42, 45, 51, 0.5);
}
.ui-send .icon {
  fill: none;
  width: 32px;
  height: 32px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px;
  stroke-width: 1.5px;
}
@media (max-width: 420px) {
  .ui-send .icon {
    width: 24px;
    height: 24px;
    margin-right: 4px;
  }
}

.ui-input {
  border: none;
  border-bottom: 1px solid #BBDBCC;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 12px;
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 144.444%;
  outline: none;
  width: 100%;
}
.ui-input::placeholder {
  color: #1F2839;
}
.ui-input-file {
  display: none;
}

.ui-textarea {
  border: none;
  border-bottom: 1px solid #BBDBCC;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 12px;
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 144.444%;
  outline: none;
  width: 100%;
  resize: none;
  max-height: 150px;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.ui-textarea::placeholder {
  color: #1F2839;
}
.ui-textarea::-webkit-scrollbar {
  display: none;
}

.pagination {
  display: flex;
  column-gap: 40px;
  margin-bottom: 36px;
}
.pagination-nav {
  position: relative;
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid #E3E8F2;
  transition: 0.3s border-color;
}
.pagination-nav:hover {
  border-color: #15355C;
}
.pagination-nav-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 24px;
  height: 24px;
  filter: brightness(0.2) saturate(21.5);
}
.pagination-nav-icon__prev {
  transform: translate(-50%, -50%) rotate(-180deg);
}
.pagination-list {
  display: flex;
  align-items: center;
  column-gap: 20px;
}
.pagination-item {
  text-align: center;
  font-size: 20px;
  font-size: 1.25rem;
}
.pagination-item-link {
  display: inline-block;
  width: 48px;
  height: 48px;
  line-height: 48px;
  border-radius: 50%;
  border: 1px solid #E3E8F2;
  transition: 0.3s border-color, 0.3s color, 0.3s background-color;
  color: #9EA5B4;
}
.pagination-item-link.active {
  background-color: #2861CF;
  color: #FFFFFF;
  border-color: #2861CF;
}
.pagination-item-link:hover {
  background-color: #FFFFFF;
  color: #15355C;
  border-color: #15355C;
}

.ui-select {
  cursor: pointer;
  transition: filter 0.3s;
}
.ui-select:hover .jq-selectbox__trigger-arrow {
  background-image: url(../images/svg/icon-chevron-down_blue.svg);
}
.ui-select:hover .jq-selectbox__select-text {
  color: #2861CF;
}
.ui-select.opened .jq-selectbox__trigger-arrow {
  background-image: url(../images/svg/icon-chevron-down_blue.svg);
}
.ui-select.opened .jq-selectbox__select-text {
  color: #2861CF;
}
.ui-select.opened .jq-selectbox__trigger-arrow {
  transform: rotate(180deg);
}

.jq-selectbox__select-text {
  display: inline-block;
  vertical-align: middle;
  padding-right: 10px;
  transition: color 0.3s;
}

.jq-selectbox__trigger {
  display: inline-block;
  vertical-align: middle;
  transition: filter 0.3s;
}

.jq-selectbox__trigger-arrow {
  background-image: url(../images/svg/icon-chevron-down.svg);
  background-repeat: no-repeat;
  background-position: center;
  width: 20px;
  height: 20px;
  background-size: 20px 20px;
  transition: transform 0.3s, background-image 0.3s;
}

.jq-selectbox__dropdown {
  background-color: #FFFFFF;
  left: 0;
  border-radius: 5px;
  box-shadow: -4px 2px 8.7px 0px rgba(36, 64, 130, 0.06);
}

.jq-selectbox li {
  transition: color 0.3s;
  padding: 10px 20px;
}
.jq-selectbox li:not(:last-child) {
  border-bottom: 1px solid #E3E8F2;
}
.jq-selectbox li:hover {
  color: #2861CF;
}

.breadcrumbs {
  margin-bottom: 40px;
}
.breadcrumbs li {
  display: inline-block;
  margin-right: 34px;
  position: relative;
  font-size: 14px;
  font-size: 0.875rem;
}
.breadcrumbs li a {
  transition: color 0.3s;
}
.breadcrumbs li a:hover {
  color: #2861CF;
}
.breadcrumbs li:not(:last-child)::after {
  position: absolute;
  top: 50%;
  right: -20px;
  transform: translate(50%, -50%);
  content: "";
  background-image: url(../images/svg/icon-chevron-right.svg);
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 18px 18px;
}
.breadcrumbs li:last-child {
  color: #9EA5B4;
}

.ui-poor-vision {
  filter: grayscale(1);
}

