  margin-bottom: 30px;
  border-radius: 5px;
}
.video-gallery-card-image {
  border-radius: 5px;
  overflow: hidden;
  aspect-ratio: 1.52;
}
.video-gallery-card-image img {
  width: 100%;
}
.video-gallery-card-play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer-top {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #E3E8F2;
  padding-top: 20px;
  padding-bottom: 30px;
}
.footer-logo {
  margin-right: 40px;
  transition: opacity 0.3s;
  min-width: 23%;
  max-width: 23%;
}
.footer-logo:hover {
  opacity: 0.9;
}
.footer-contacts {
  margin-left: auto;
  margin-right: 20px;
  font-size: 0;
}
.footer-contacts-item {
  display: inline-block;
  margin-right: 40px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.footer-contacts-item:nth-last-child(2) {
  margin-right: 60px;
}
.footer-contacts-title {
  color: #9EA5B4;
  font-size: 14px;
  font-size: 0.875rem;
}
.footer-contacts-link {
  font-size: 22px;
  font-size: 1.375rem;
  font-weight: 500;
  transition: color 0.3s;
  white-space: nowrap;
}
.footer-contacts-link:hover {
  color: #2861CF;
}
.footer-socials {
  font-size: 0;
  white-space: nowrap;
}
.footer-socials-item {
  position: relative;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid #E3E8F2;
  display: inline-block;
  background-color: #FFFFFF;
  cursor: pointer;
  transition: background-color 0.3s;
}
.footer-socials-item:not(:last-child) {
  margin-right: 20px;
}
.footer-socials-item:hover {
  background-color: #2861CF;
}
.footer-socials-item:hover .footer-socials-icon {
  fill: #FFFFFF;
}
.footer-socials-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 26px;
  height: 26px;
  fill: #2861CF;
  transition: fill 0.3s;
}
.footer-middle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 40px;
  padding-bottom: 40px;
}
.footer-middle-links {
  font-size: 0;
}
.footer-middle-links-item {
  display: inline-block;
  height: 48px;
  filter: grayscale(100%);
  opacity: 0.8;
  transition: filter 0.3s, opacity 0.3s;
}
.footer-middle-links-item:not(:last-child) {
  margin-right: 20px;
}
.footer-middle-links-item:hover {
  filter: grayscale(0%);
  opacity: 1;
}
.footer-nav ul {
  display: flex;
  gap: 40px;
}
.footer-nav li a {
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.11;
  text-transform: uppercase;
  transition: color 0.3s;
}
.footer-nav li a:hover {
  color: #2861CF;
}
.footer-bottom {
  padding-top: 16px;
  padding-bottom: 40px;
  font-size: 14px;
  font-size: 0.875rem;
  color: #9EA5B4;
  display: flex;
  justify-content: space-between;
}
.footer-bottom-copy {
  text-transform: uppercase;
}
.footer-bottom-link {
  color: #9EA5B4;
  margin-left: 24px;
  transition: color 0.3s;
}
.footer-bottom-link:hover {
  color: #2861CF;
}

